  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
  margin: 0;
  font-family: Merriweather, serif;
}

body:before {
  content: "";
  z-index: -1;
  background: #00000080;
