  pointer-events: none;
  background: #000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: 5s infinite colors;
  display: none;
  position: fixed;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px #fff, 0 0 60px #fff, 0 0 100px #fff;
}

.obj-width {
  max-width: 1200px;
  margin: auto;
}

.cursor:before {
  content: "";
  opacity: .2;
  background: #f5f5f5;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  position: absolute;
  transform: translate(-30%, -30%);
}

nav {
  backdrop-filter: blur(10px);
  z-index: 1;
  background-color: #0000;
  position: relative;
  box-shadow: 10px 10px 10px #000;
}

.navbar {
