  }

  50% {
    color: #000;
  }
}

.logo {
  color: #f5f5f5;
  z-index: 999;
  text-shadow: 0 0 20px #bfa89a, 0 0 60px #bfa89a, 0 0 100px #bfa89a;
  font-weight: bolder;
  animation: 5s infinite colors;
}

.logo:before {
  opacity: .2;
  position: absolute;
  transform: translate(-50%, -50%);
}

#search-input {
  color: #000;
  background: #ffffff80;
  border: none;
  border-radius: 5px;
  max-width: 400px;
  padding: 10px;
  font-size: 18px;
}

#search-button, #category-filter-button {
  color: #000;
  cursor: pointer;
  background: #ffffff80;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
