  cursor: pointer;
  background: #ffffff80;
  border: none;
  width: 100%;
  padding: 10px;
  font-size: 18px;
}

#category-filter-list button:hover {
  background-color: #ccc;
}

#category-filter-button:hover + #category-filter-list {
  display: block;
}

#blog-container {
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: stretch;
  padding: 3rem 0;
  display: flex;
}

.blog-card {
  backdrop-filter: blur(10px);
  background-color: #0000;
  border: 2px solid #000;
  border-radius: 10px;
  width: 67%;
